export default Work

function Modal1(props:any) {
  return (
    <Modal
      {...props}
      size="lg"
      dialogClassName='modal-90w'
      aria-labelledby="example-custom-modal-styling-title"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="example-custom-modal-styling-title">
          포트폴리오
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h4>PORTFOLIO</h4>
        <p>
          본 프로젝트는 React.js와 TypeScript로 구현하여 만든 사이트입니다. <br />
          다양한 라이브러리와 JavaScript, css 등 그간 배웠던 것들을 이용하여 만들었습니다.
        </p>
        <div></div>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}

function Modal2(props:any) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          T1 Shop
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h4>메인 페이지</h4>
        <p>
          본 프로젝트는 React, scss, redux를 사용하여 만든 쇼핑몰 웹사이트 입니다. <br />
          리액트 라우터를 사용하여 페이지를 이동하게 구현하였고, 각각의 페이지는 컴포넌트화 하여 재사용성을 높였습니다. <br />
          또한, 상품 디테일 페이지는 리덕스를 사용하여 데이터의 상태관리를 구현했습니다. <br />
          웹디자인의 경우에는 각 카테고리별로 많은 제품들을 볼 수 있게 구현했고, 버튼을 클릭함으로써 제품들이 원하는 조건으로 정렬되도록 구현했습니다.
        </p>
        <div className='text-center'>
          <img src={process.env.PUBLIC_URL + "/img/T1Shop-main.gif"} alt="main" width={'80%'} style={modalBorder}/>
        </div>
        <hr />
        <h4>상품 페이지</h4>
        <p>
          redux를 활용한 페이지입니다. 제품의 상세페이지에서 선택한 정보들&#40;가격, 수량 등&#41;은 redux를 통해 장바구니 페이지에서 확인 가능합니다. <br /> 간단한 수량 추가와 삭제 기능 그리고 상품의 갯수에 따른 총 결제금액의 변화도 구현하였습니다.
        </p>
        <div className='text-center'>
          <img src={process.env.PUBLIC_URL + "/img/T1Shop-detail.gif"} alt="detail" width={'80%'} style={modalBorder}/>
        </div>
        <hr />
        <h4>장바구니, 로그인 페이지</h4>
        <p>
          정규표현식을 이용하여 로그인 페이지에 입력받은 정보를 확인하고, 조건에 맞지 않는 입력의 경우엔 경고창을 출력하도록 구현했습니다.
        </p>
        <div className='text-center'>
          <img src={process.env.PUBLIC_URL + "/img/T1Shop-cart.gif"} alt="detail" width={'80%'} style={modalBorder}/>
        </div>
        <div className='text-center mt-3'>
          <Button onClick={()=>{window.open('https://alanpark727.github.io/project_05')}}> 사이트 바로가기 </Button>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button variant='outline-primary' onClick={()=>{window.open('https://alanpark727.github.io/project_05/박동열_project_05.pdf')}}> More... </Button>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}

